import styled from 'styled-components'

const base = '#0f1923'
const white = '#ece8e1'
const pink = '#ff4655'
const height = '54px'
const transition = '0.3s ease-out all'

export const IntroInterfaceWrapper = styled.div`
  width: 100vw;
  height: 100vh;
`

export const Button = styled.div`
  display: flex;
  width: 100vw;
  height: 90vh;
  align-items: center;
  justify-content: flex-end;
  flex-flow: column;
  z-index: 2;
  & div {
    z-index: 3;
    width: 100%;
    max-width: 240px;
    height: ${height};
    padding: 8px;
    font-size: 0.8rem;
    font-weight: 900;
    color: ${base};
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    box-shadow: 0px 0px 0px 1px inset rgba(236, 232, 225, 0.8);
    position: relative;
    margin: 10px 0;
    &.white:hover > p {
      color: ${white};
    }
    &.white > p {
      background: ${white};
      color: $base;
      & span.base {
        border: 1px solid transparent;
      }
    }
    &:after,
    &:before {
      content: '';
      width: 1px;
      position: absolute;
      height: 8px;
      background: ${base};
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &:before {
      right: 0;
      left: initial;
    }
    & p {
      margin: 0;
      height: ${height};
      line-height: ${height};
      box-sizing: border-box;
      z-index: 3;
      left: 0;
      width: 100%;
      position: relative;
      overflow: hidden;
      & span.base {
        box-sizing: border-box;
        position: absolute;
        z-index: 4;
        width: 100%;
        height: 100%;
        left: 0;
        border: 1px solid ${pink};
        &:before {
          content: '';
          width: 2px;
          height: 2px;
          left: -1px;
          top: -1px;
          background: ${base};
          position: absolute;
          transition: ${transition};
        }
      }
      & span.bg {
        left: -5%;
        position: absolute;
        background: ${base};
        width: 0;
        height: 100%;
        z-index: 5;
        transition: ${transition};
        transform: skewX(-10deg);
      }
      & span.text {
        z-index: 6;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        &:after {
          content: '';
          width: 4px;
          height: 4px;
          right: 0;
          bottom: 0;
          background: ${base};
          position: absolute;
          transition: ${transition};
          z-index: 7;
        }
      }
    }
    &:hover {
      color: ${white};
      & span.bg {
        width: 110%;
      }
      & span.text:after {
        background: ${white};
      }
    }
  }
`
