import React from 'react'
import { IntroInterfaceWrapper, Button } from './introinterface.styles'

const IntroInterface = ({ reference }) => {
  const handleClick = (ref) =>
    ref.current.scrollIntoView({ behavior: 'smooth' })
  return (
    <IntroInterfaceWrapper>
      <Button
        onClick={() => {
          handleClick(reference)
        }}
      >
        <div className='white'>
          <p>
            <span className='bg'></span>
            <span className='base'></span>
            <span className='text'>Conoce mi historia</span>
          </p>
        </div>
      </Button>
    </IntroInterfaceWrapper>
  )
}

export default IntroInterface
