import styled from 'styled-components'

const base = '#0f1923'
const white = '#ece8e1'
const pink = '#ff4655'
const height = '54px'
const transition = '0.3s ease-out all'

export const Paper = styled.div`
  border-radius: 2px;
  position: relative;
  height: 550px;
  width: 450px;
  background: rgba(255, 255, 255, 0.9);
  margin: -275px -225px;
  right: -80%;
  top: 45%;
  box-shadow: 0px 0px 5px 0px #888;
  &::before,
  &::after {
    content: '';
    position: relative;
    bottom: 10px;
    width: 40%;
    height: 10px;
    box-shadow: 0 5px 14px rgba(0, 0, 0, 0.7);
    z-index: -1;
    transition: all 0.3s ease-in-out;
  }
  &::before {
    transform: skew(-5deg) rotate(-5deg);
  }
  &::after {
    right: 15px;
    transform: skew(5deg) rotate(5deg);
  }
  &:hover::before,
  &:hover::after {
    box-shadow: 0 2px 14px rgba(0, 0, 0, 0.4);
  }

  &:hover::before {
    left: 5px;
  }

  &:hover::after {
    right: 5px;
  }
  .lines {
    font-family: 'Noto Sans JP', sans-serif;
    margin-top: 40px;
    height: calc(100% - 40px);
    width: 100%;
    background-image: repeating-linear-gradient(
      white 0px,
      white 24px,
      steelblue 25px
    );
  }
  .text {
    position: absolute;
    top: 65px;
    left: 45px;
    bottom: 10px;
    right: 20px;
    line-height: 25px;
    overflow: hidden;
    outline: none;
  }
`

export const IntroInterfaceWrapper = styled.div`
  width: 100vw;
  height: 100vh;
`

export const Button = styled.div`
  cursor: pointer;
  z-index: 100;
  & div {
    z-index: 3;
    width: 4rem;
    height: ${height};
    padding: 8px;
    font-size: 0.8rem;
    font-weight: 900;
    color: ${base};
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    box-shadow: 0px 0px 0px 1px inset rgba(236, 232, 225, 0.8);
    position: relative;
    margin: 10px 0;
    &.white:hover > p {
      color: ${white};
    }
    &.white > p {
      background: ${white};
      color: ${base};
      & span.base {
        border: 1px solid transparent;
      }
    }
    &:after,
    &:before {
      content: '';
      width: 1px;
      position: absolute;
      height: 8px;
      background: ${base};
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &:before {
      right: 0;
      left: initial;
    }
    & p {
      margin: 0;
      height: ${height};
      line-height: ${height};
      box-sizing: border-box;
      z-index: 3;
      left: 0;
      width: 100%;
      position: relative;
      overflow: hidden;
      & span.base {
        box-sizing: border-box;
        position: absolute;
        z-index: 4;
        width: 100%;
        height: 100%;
        left: 0;
        border: 1px solid ${pink};
        &:before {
          content: '';
          width: 2px;
          height: 2px;
          left: -1px;
          top: -1px;
          background: ${base};
          position: absolute;
          transition: ${transition};
        }
      }
      & span.bg {
        left: -5%;
        position: absolute;
        background: ${base};
        width: 0;
        height: 100%;
        z-index: 5;
        transition: ${transition};
        transform: skewX(-10deg);
      }
      & span.text {
        z-index: 6;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        &:after {
          content: '';
          width: 4px;
          height: 4px;
          right: 0;
          bottom: 0;
          background: ${base};
          position: absolute;
          transition: ${transition};
          z-index: 7;
        }
      }
    }
    &:hover {
      color: ${white};
      & span.bg {
        width: 110%;
      }
      & span.text:after {
        background: ${white};
      }
    }
  }
`
export const ControlWrapper = styled.div`
  width: 5rem;
  height: 100vh;
  position: relative;
  top: 0;
  left: 50%;
`

export const StoryWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 100%;
  left: 0;
`
