import styled from 'styled-components'
import she from '../../assets/she_good.png'
import sheBad from '../../assets/she_bad.png'

export const FullWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 100%;
`

export const BookWrapper = styled.div`
  z-index: 100;
  width: 45vw;
  height: 100vh;
  position: relative;
  top: 0;
  left: 0;
  .c {
    padding: 1rem;
    box-shadow: 0px 0px 0px 1px inset rgba(236, 232, 225, 0.8);
    position: absolute;
    top: 15%;
    left: 13%;
    max-width: 500px;
    max-height: 500px;
    width: 50ch;
    height: 50ch;
    cursor: pointer;
    will-change: transform, opacity;
  }
`
export const CardBack = styled.div`
  width: 100%;
  height: 100%;
  background-size: cover;
  background: url(${she});
`

export const CardFront = styled.div`
width:100%;
height:100%;
  background-size: cover;
  background: url(${sheBad});
  background-size: 800px 500px;
  }
`
