import React, { useState } from 'react'
import { Paper, ControlWrapper, Button, StoryWrapper } from './story.styles'

const Deck = ({ isShe }) => {
  const [current, setCurrent] = useState(0)
  const handleCurrent = (index) => {
    setCurrent(index)
  }
  return (
    <StoryWrapper>
      <Paper>
        <div className='lines'>
          <div className='text'>
            {isShe ? (
              <>
                {sheStory[current].date} <br />
                <br />
                {sheStory[current].message}
              </>
            ) : (
              <>
                {heStory[current].date} <br />
                <br />
                {heStory[current].message}
              </>
            )}
          </div>
        </div>
      </Paper>
      <ControlWrapper>
        {isShe ? (
          <>
            <Button onClick={() => handleCurrent(0)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>1</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(1)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>2</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(2)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>3</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(3)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>4</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(4)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>5</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(5)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>6</span>
                </p>
              </div>
            </Button>
          </>
        ) : (
          <>
            <Button onClick={() => handleCurrent(0)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>1</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(1)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>2</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(2)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>3</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(3)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>4</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(4)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>5</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(5)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>6</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(6)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>7</span>
                </p>
              </div>
            </Button>
            <Button onClick={() => handleCurrent(7)}>
              <div className='white'>
                <p>
                  <span className='bg'></span>
                  <span className='base'></span>
                  <span className='text'>8</span>
                </p>
              </div>
            </Button>
          </>
        )}
      </ControlWrapper>
    </StoryWrapper>
  )
}

export default Deck

const sheStory = [
  {
    date: 'El inicio del final...',
    message:
      'Un día de verano, es especialmente caluroso salí en bicicleta hacia el pueblo con mi lista de compras para cocinar la tradicional cena de cumpleaños, en casa cada año cocinamos juntos para la familia con mis abuelos, mis tíos y mis primos. En casa mis padres están empezando a preparar sus platillos estrellas mientras regreso a casa con lo que falta. ',
  },
  {
    date: '',
    message:
      'Durante el camino a la tienda cae el sol, y aunque hay brisa no es lo suficientemente refrescante, así que elijo ir por la ruta más corta para agilizar mi tarea, entonces tengo la oportunidad de ver las personas caminando en la calle y a lo lejos logro reconocer a Elizabeth con su hermana pequeña, parece ser que la pequeña intenta convencer a Eli para comprar dulces, como siempre cada vez que tiene la oportunidad de pasar por aquella tienda; pienso en ir a saludarlas pero antes de tomar una decisión ya están regresando a casa. ',
  },
  {
    date: '',
    message:
      'Recuerdo de inmediato mi tarea principal; entro a la tienda del señor Fizt, toda mi vida ha tenido esta tienda y siempre compramos aquí porque tiene productos de su propia granja, a la abuela se le hincha el pecho de orgullo cuando pregunta por los ingredientes y escucha que los compramos del señor Fizt. Tomó rápidamente los ingredientes de la lista, mermelada, cebollín, algunos tomates y fresas.',
  },
  {
    date: '',
    message:
      'Como siempre el Sr Fizt me saluda cálidamente, pregunta sobre mi familia y también sobre la particularidad de los ingredientes que llevamos, mientras le explico giro mi cabeza hacia la ventana y logro observar cómo se encienden las luces de la calle, mientras empiezo a reconocer rostros familiares, cuando menos lo pienso he terminado con mi explicación, tomo lo pedido y lo pongo las cosas en la canasta de la bicicleta.',
  },
  {
    date: '',
    message:
      'De camino a casa noto como de repente oscurece tan pronto y lo lejos que estoy en mi recorrido a casa, solo espero llegar a tiempo para terminar la receta y recibir a mi familia que según mis cálculos deberían llegar pronto.',
  },
  {
    date: 'Ese día, el día X',
    message:
      'Pocos más de un kilómetro había pasado cuando vi un carro estacionado ocupando la calle, una van negra y con bordes oxidados. Mi primer pensamiento fue que se había varado y claramente en medio de la nada no podían moverse sin antes poder contactar con alguien de servicios para movilizar en grúa. Me decidí acercar a ver si la persona en su problema automovilístico requería de alguna ayuda, grave error....',
  },
]

const heStory = [
  {
    date: 'Un díá cualquiera',
    message:
      'En el centro de la ciudad hay una pastelería que no destaca mucho pero es lo suficientemente discreta para pasar desapercibido, observó el reloj 6:47 p.m. sé que no debería tardar mucho en aparecer, es un hábito de cada año, siempre se les olvida comprar algún ingrediente y Lise es quien sale a salvar la cena en su bicicleta, que parece que la ha usado toda la vida, en un regalo de navidad, quizás es un detalle que pueda usar en el futuro y lo anoto en mi libreta. ',
  },
  {
    date: '',
    message:
      'Dos minutos después en la curva se revela la silueta de una bicicleta, y cuando está un poco más cerca noto que se distrae pero solo logro distinguir a otra chica a lo lejos de pie frente a una tienda de dulces, no es de sorprender porque es la única a su alrededor y funciona como un iman de niños, con sus colores llamativos y un montón de dibujos que se solo verlos me provoca un mareo.',
  },
  {
    date: '',
    message:
      'Entre colores y personas pasando, tengo surte desde la silla donde estoy y noto que se trata de su amiga; quizá una de las más cercanas, se trata de la hermana del medio en su familia, su hermano mayor es un poco torpe para lo que dice prometer, probablemente termine como su tío, metido en la taberna de mala muerte, si es lista cuando tenga la oportunidad la aprovechará para salir de ese lugar y que no termine en uno de esos matrimonios arreglados con un amigo de su padre, pero es un poco pronto para saberlo.',
  },
  {
    date: '',
    message:
      'Desde aquí noto como repentinamente Catania de parecer decide entrar a una tienda, entonces decido tomar ventaja de su rutina y avanzar en su ruta para seguir pasando desapercibido, pago mi cuenta, tomo mi libreta y tomo el camino en que apareció la chica en un principio. Es algo oscuro y solitario pero tengo un punto perfecto para rectificar que llegue a tiempo para la cena. ',
  },
  {
    date: 'ella, ella... ELLA',
    message:
      'Lise tiene como vecinos una pareja vieja, llevan ahí toda su vida y confían ciegamente en la estructura de su casa, yo confío en su vacío sótano con difícil acceso para su edad y claro el hecho de que hago arreglos ocasionalmente para ellos, así que como parte de su pago me permiten usar sótano como despacho personal es mi segundo despacho, tiene muy pocas cosas y el olor deja mucho que desear pero tiene una vista directa hacia la casa de Lise',
  },
  {
    date: '',
    message:
      'Después de un rato veo cómo la pequeña ventana deja entrar mucha luz al sótano, me asomo discretamente diagonal y observo como se ilumina la calle, han llegado sus abuelos. De inmediato una señora con cabello blanco sale del auto cuando este se detiene frente a la casa y sonríe a lo lejos, justo a tiempo para recibir a Lise con un abrazo.',
  },
  {
    date: '',
    message:
      'Reviso mi libreta y marco con un chulo al lado de ‘cena familiar’ son las exactamente las 7:35 tal y cómo está planeado.',
  },
  {
    date: 'finalmente, una noche antes',
    message: 'y bien Lise, ¿te interesaría una cita?',
  },
]
