import React, { useEffect } from 'react'
import {
  IntroBackground,
  Div,
  Span,
  Stockholm,
  Loader,
  LoaderWrapper,
  IntroWrapper,
  FogWrapper,
} from './intro.styles'
import smoke from '../../assets/smoke.mp4'
import IntroInterface from '../IntroInterface'

const Intro = ({ showLoading, setShowLoading, reference }) => {
  useEffect(() => {
    let timer1 = setTimeout(() => setShowLoading(false), 3000)
    return () => {
      clearTimeout(timer1)
    }
  }, [setShowLoading])

  if (showLoading) {
    return (
      <LoaderWrapper>
        <Loader>
          <div />
        </Loader>
      </LoaderWrapper>
    )
  } else {
    return (
      <IntroWrapper>
        <IntroInterface reference={reference} />
        <FogWrapper>
          <div id='foglayer_01' className='fog'>
            <div className='image01'></div>
            <div className='image02'></div>
          </div>
          <div id='foglayer_02' className='fog'>
            <div className='image01'></div>
            <div className='image02'></div>
          </div>
          <div id='foglayer_03' className='fog'>
            <div className='image01'></div>
            <div className='image02'></div>
          </div>
        </FogWrapper>
        <IntroBackground>
          <video src={smoke} autoPlay muted></video>
          <Div>
            <div>
              <Span delay='1.5s'>H</Span>
              <Span delay='2.5s'>A</Span>
              <Span delay='3.5s'>Z</Span>
              <Span delay='3.8s'>E</Span>
            </div>
            <div>
              <Stockholm>STOCKHOLM</Stockholm>
            </div>
          </Div>
        </IntroBackground>
      </IntroWrapper>
    )
  }
}

export default Intro
