import styled, { keyframes } from 'styled-components'

const animate = keyframes`
    from{
        opacity: 0;
        transform: rotateY(90deg);
        filter:blur(10px);
    } to{
        opacity: 1;
        transform: rotateY(0deg);
        filter:blue(0px);
    }
`

const opacity = keyframes`
    50%{
        opacity:1;
  } 100%{
        opacity:0;
  }
`

const load = keyframes`
0%, 100% {
    animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }
  0% {
    transform: rotateY(0deg);
  }
  50% {
    transform: rotateY(1800deg);
    animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }
  100% {
    transform: rotateY(3600deg);
  }
`
const entrance = keyframes`
    from{
        opacity: 0;
        filter:blur(10px);
    } to{
        opacity: 1;
        filter:blue(0px);
    }
`

export const FogWrapper = styled.div`
  z-index: 1;
  opacity: 0;
  height: 100%;
  position: fixed;
  top: 0;
  width: 100%;
  -webkit-filter: blur(1px) grayscale(0.2) saturate(1.2) sepia(0.2);
  filter: blur(1px) grayscale(0.2) saturate(1.2) sepia(0.2);
  animation: ${entrance} 3s linear forwards;
`

export const LoaderWrapper = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: ${opacity} 3s linear ease-in;
`

export const Loader = styled.div`
  display: inline-block;
  transform: translateZ(1px);
  div {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    margin: 8px;
    border-radius: 50%;
    background: #fff;
    animation: ${load} 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
`

export const IntroWrapper = styled.div`
  opacity: 0;
  animation: ${entrance} 1s linear forwards;
  overflow: hidden;
`

export const IntroBackground = styled.div`
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  color: #ddd;
  text-align: center;
  font-size: 3em;
  font-weight: 900;
  video {
    object-fit: cover;
  }
`

export const Div = styled.div`
  margin: 0;
  padding: 0;
  position: absolute;
  transform: translateY(-20%);
  width: 100%;
  text-align: center;
  top: 50%;
  letter-spacing: 2rem;
`

export const Span = styled.div`
  opacity: 0;
  display: inline-block;
  animation: ${animate} 1s linear forwards;
  animation-delay: ${(props) => props.delay};
`
export const Stockholm = styled.p`
  opacity: 0;
  margin: 0 0 0 -1rem;
  padding: 0;
  letter-spacing: 0;
  animation: ${entrance} 1s linear forwards;
  animation-delay: 3s;
`
