import React from 'react'
import { useSpring, animated as a } from 'react-spring'
import { BookWrapper, CardBack, CardFront } from './book.styles'

const Book = ({ reference, flipped, set }) => {
  const { transform, opacity } = useSpring({
    opacity: flipped ? 1 : 0,
    transform: `perspective(1000px) rotateX(${flipped ? 180 : 0}deg) `,
    config: { mass: 1, tension: 1500, friction: 150 },
  })
  return (
    <BookWrapper ref={reference}>
      <a.div
        onClick={() => set((state) => !state)}
        className='c back'
        style={{ opacity: opacity.interpolate((o) => 1 - o), transform }}
      >
        <CardBack />
      </a.div>

      <a.div
        onClick={() => set((state) => !state)}
        className='c front'
        style={{
          opacity,
          transform: transform.interpolate((t) => `${t} rotateX(180deg) `),
        }}
      >
        <CardFront />
      </a.div>
    </BookWrapper>
  )
}

export default Book
